* {
  box-sizing: border-box;
}
html {
  width: 100%;
  height: 100%;
  background-color: orange;
  padding: 0;
  margin: 0;
}
body {
  padding: 10px 16px;
  margin: 0;
  background: linear-gradient(#ffe025, orange);
}
.logo {
  width: 160px;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 30px;
}
.panel {
  position: relative;
  width: 100%;
  min-height: 50px;
  background-color: #fff;
  margin-top: 20px;
  border-radius: 6px;
  box-shadow: 0 0 5px 2px rgba(230, 157, 42, 0.08);
  padding: 15px 20px;
  text-align: left;
  &:after {
    content: " ";
    display: block;
    clear: both;
  }
}
.connect-panel {
  p {
    color: #999;
  }
}
.el-row {
  margin-bottom: 15px;
}
.command-panel {
  .button-wrap {
    float: left;
    margin: 0 10px 10px 0;
  }
}
.panel-title {
  width: 100%;
  height: 30px;
  text-align: left;
  font-size: 16px;
  margin-bottom: 12px;
  font-weight: bold;
}
.info-group {
  position: relative;
  float: left;
  display: block;
  text-align: left;
  width: 48%;
  margin-bottom: 20px;
  margin-right: 1%;
  overflow: hidden;
  min-height: 45px;
  .label {
    font-size: 14px;
    font-weight: bold;
    color: #758699;
  }
  .value {
    width: 96%;
    font-size: 14px;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}
.code {
  display: block;
  width: 100%;
  height: 100px;
  background-color: #333333;
  color: #e4edff;
  padding: 6px 10px;
}
.copyright {
  width: 100%;
  color: white;
  padding: 20px 0;
  a {
    color: white;
  }
}
@media screen and (min-width: 900px) {
  .panel-container {
    display: flex;
    justify-content: space-between;
  }
  .panel {
    margin-right: 10px;
  }
  .copyright {
    position: fixed;
    bottom: 10px;
  }
}
